.Toolbar {
    height: 56px;
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    background-color: #68a51a;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 20px;
    box-sizing: border-box;
    z-index: 90;
}

.Toolbar nav {
    height: 100%;
}

.Logo {
    height: 90%;
    padding-top: 1%;
    padding-bottom: 1%;

}

.AppTitle {
    text-align: center;
    color: white;
    font-size: 30px;
    font-weight: 400;
    font-family: ArialRoundedMTStd,  'Nunito', Arial, sans-serif!important;
}


@media (max-width: 499px) {
    .DesktopOnly {
        display: none;
    }

}
